body {
  color: #333;
  background-image: url("https://res.cloudinary.com/doka2wyj0/image/upload/v1727497646/ja/icon/yem1fwofrhvkqi7k191f.jpg");
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  display: flex;
}

header {
  color: #fff;
  background-color: #000c;
  flex-direction: column;
  align-items: center;
  padding: 40px 20px;
  display: flex;
  box-shadow: 0 4px 10px #0003;
}

h1 {
  text-transform: uppercase;
  letter-spacing: 2px;
  margin: 0;
  font-size: 36px;
  font-weight: bold;
}

nav ul {
  gap: 30px;
  margin: 20px 0;
  padding: 0;
  list-style: none;
  display: flex;
}

nav ul li {
  display: inline;
}

nav a {
  color: #fff;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 600;
  text-decoration: none;
  transition: color .3s;
}

nav a:hover {
  color: #007bff;
}

.search-bar {
  flex-direction: column;
  align-items: center;
  width: 400px;
  margin-top: 30px;
  display: flex;
  position: relative;
}

.search-bar input {
  border: 1px solid #ddd;
  border-radius: 30px;
  outline: none;
  width: 100%;
  padding: 12px;
  font-size: 16px;
  transition: all .3s;
  box-shadow: 0 2px 4px #0000001a;
}

.search-bar input:focus {
  border-color: #007bff;
  box-shadow: 0 4px 8px #007bff33;
}

.search-bar button {
  background: none;
  border: none;
  position: absolute;
  top: 12px;
  right: 15px;
}

.search-bar button img {
  width: 24px;
}

#suggestions-container {
  z-index: 1000;
  background-color: #000;
  border: 1px solid #ddd;
  border-radius: 5px;
  width: 100%;
  max-height: 220px;
  display: none;
  position: absolute;
  top: 60px;
  overflow-y: auto;
  box-shadow: 0 4px 8px #0000001a;
}

.suggestion {
  cursor: pointer;
  border-bottom: 1px solid #f1f1f1;
  padding: 12px 15px;
  font-size: 15px;
}

.suggestion:last-child {
  border-bottom: none;
}

.suggestion:hover {
  background-color: #f1f1f1;
}

#reviews-container {
  flex-direction: column;
  flex-grow: 1;
  gap: 30px;
  max-width: 1200px;
  margin: 40px auto;
  display: flex;
}

.card {
  background-color: #fff;
  border: 1px solid #e3e3e3;
  border-radius: 12px;
  align-items: center;
  padding: 30px;
  transition: transform .3s;
  display: flex;
  box-shadow: 0 6px 15px #0000001a;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px #00000026;
}

.image {
  flex-shrink: 0;
  width: 220px;
  height: 200px;
  margin-right: 20px;
}

.book-cover {
  object-fit: cover;
  border-radius: 8px;
  width: 100%;
  height: 100%;
}

.card__info {
  flex-grow: 1;
}

.title {
  color: #007bff;
  margin-bottom: 10px;
  font-size: 22px;
  font-weight: 700;
  text-decoration: none;
}

.title:hover {
  text-decoration: underline;
}

.author {
  color: #555;
  font-size: 18px;
}

.description {
  color: #666;
  margin: 12px 0;
  font-size: 16px;
}

.buy-link {
  color: #007bff;
  font-size: 15px;
  font-weight: 600;
  text-decoration: none;
}

.buy-link:hover {
  text-decoration: underline;
}

footer {
  color: #fff;
  text-align: center;
  background-color: #000000e6;
  margin-top: 20px;
  padding: 20px;
  font-size: 14px;
}

@media (width <= 768px) {
  .card {
    text-align: center;
    flex-direction: column;
    height: auto;
  }

  .image {
    width: 100%;
    height: auto;
    margin-bottom: 15px;
  }

  .title {
    font-size: 20px;
  }
}
